.main {
  position: relative;
  z-index: 1;
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #000000;
  color: #ffffff;
  padding: 175px 0 250px;
  &__inner {
    padding-left: 80px;
  }
  &__title {
    font-size: 90px;
    line-height: 110%;
    font-weight: 700;
    text-transform: uppercase;
    span {
      display: block;
      font-size: 148px;
      line-height: 100%;
    }
  }
  &__subtitle {
    display: flex;
    align-items: center;
    margin-top: 50px;
    i {
      flex-shrink: 0;
      width: 19px;
      margin-right: 8px;
      img {
        width: 100%;
      }
    }
    p {
      font-size: 18px;
      line-height: 130%;
      color: #F8F8F8;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px 40px;
    margin-top: 50px;
  }
  &__btn {
    position: relative;
    height: 56px;
    border: 2px solid #FFF;
    border-radius: 2px;
    padding: 3px 20px;
    overflow: hidden;
    .main__btn-text {
      position: relative;
      z-index: 1;
      font-size: 20px;
      line-height: 130%;
      font-weight: 700;
      text-transform: uppercase;
    }
    .btn-animation {
      position: absolute;
      z-index: 0;
      display: block;
      width: 0;
      height: 0;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      background-color: #C5EB2C;
      transition: width 0.8s, height 0.8s;
    }
    &:hover {
      .btn-animation {
        width: 700px;
        height: 700px;
      }
    }
  }
}
.section-group {
  position: relative;
  margin-top: -150px;
  &:after {
    content: '';
    position: absolute;
    top: -500px;
    right: -150px;
    width: 928px;
    height: 928px;
    background: radial-gradient(50% 50% at 50% 50%, #D47AEA 0%, rgba(217, 217, 217, 0.00) 100%);
    border-radius: 50%;
    opacity: 0.4;
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: -500px;
    left: -150px;
    width: 928px;
    height: 928px;
    background: radial-gradient(50% 50% at 50% 50%, #D47AEA 0%, rgba(217, 217, 217, 0.00) 100%);
    border-radius: 50%;
    opacity: 0.4;
    pointer-events: none;
  }
  &__inner{
    position: relative;
    z-index: 1;
    background: #ffffff;
    padding: 70px 36px 120px 80px;
  }
  &__img {
    position: absolute;
    right: 15px;
    bottom: calc(100% - 115px);
    width: 500px;
    img {
      width: 100%;
    }
  }
}
.level {
  &__top {
    max-width: 995px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px 40px;
  }
  .hashtag {
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #F7F7F7;
    border: 1px solid transparent;
    border-radius: 40px;
    padding: 3px 10px;
    span {
      font-size: 15px;
      line-height: 130%;
      font-weight: 500;
      text-transform: uppercase;
      color: #D47AEA;
    }
    &:hover {
      background: transparent;
      border-color: #D47AEA;
    }
  }
  .display2 {
    margin-top: 100px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin-top: 50px;
  }
  &-card {
    position: relative;
    &:hover {
      .level-card__wrap {
        box-shadow: 0px 2px 45px 0px rgba(171, 90, 191, 0.30);
      }
      .level-card__img {
        opacity: 1;
      }
    }
    &__wrap {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.70);
      border: 1px solid #E9E9E9;
      border-radius: 50px;
      padding: 32px;
    }
    &__text {
      font-size: 15px;
      line-height: 130%;
      margin-top: 15px;
      strong {
        color: #A0BC00;
      }
    }
    &__text2 {
      max-width: 320px;
    }
    &__img {
      position: absolute;
      top: -90px;
      right: 12px;
      width: 210px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    .lines {
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 10px;
      transform: translate(0, -50%);
      width: 98px;
      img {
        width: 100%;
      }
    }
  }
}
.tariffs {
  .prices {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15.4vw;
    line-height: 120%;
    font-weight: 900;
    text-transform: uppercase;
    color: #F7F7F7;
    opacity: 0.7;
    margin-left: -60px;
  }
  .display2 {
    margin-top: -200px;
  }
  &__slider {
    margin-top: 30px;
  }
  .tariffs__slider-wrap {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 730px;
      height: 730px;
      background: radial-gradient(50% 50% at 50% 50%, #D47AEA 0%, rgba(217, 217, 217, 0.00) 100%);
      border-radius: 50%;
      opacity: 0.3;
      pointer-events: none;
    }
  }
  .slider-big {
    width: calc(100% - 470px);
  }
  .slider-small {
    max-width: 470px;
    width: 100%;
    padding-right: 70px;
    .swiper-slide {
      transition: all 0.3s;
    }
    .swiper-slide-active {
      z-index: 2;
      filter: unset;
      .slider-small__btn {
        pointer-events: all;
      }
    }
    .swiper-slide-prev {
      transform: translate(0, 80px);
    }
    .swiper-slide-next {
      transform: translate(0, -80px);
    }
  }
  .slider-big__slide {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    background: #FFF;
    border: 1px solid #E9E9E9;
    border-radius: 50px;
    padding: 50px 80px;
  }
  .slider-big__left {
    width: 50%;
    max-width: 325px;
  }
  .slider-big__right {
    width: 50%;
    max-width: 300px;
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
      margin-top: 15px;
    }
    ul li {
      display: flex;
      i {
        flex-shrink: 0;
        width: 22px;
        margin-top: -1px;
        margin-right: 12px;
      }
      i img {
        width: 100%;
      }
      p {
        font-size: 15px;
        line-height: 130%;
        color: #3A3A3A;
      }
      p strong {
        color: #A0BC00;
      }
    }
  }
  .slider-big__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px / 2));
    gap: 20px 15px;
  }
  .slider-big__item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .opacity {
    opacity: 0.2;
  }
  .slider-big__item-img {
    position: relative;
    width: 100%;
    height: 106px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: auto;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 18px;
      background: #D47AEA;
      border-radius: 10px 10px 0px 0px;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 120px;
    }
  }
  .slider-big__item-title {
    text-align: center;
    font-size: 14px;
    line-height: 130%;
  }
  .slider-big__group {
    &:not(:first-child) {
      margin-top: 40px;
    }
  }
  .slider-small__slide {
    background: rgba(255, 255, 255, 0.85);
    border: 1px solid #E9E9E9;
    border-radius: 50px;
    padding: 45px 25px;
    filter: blur(4.5px);
  }
  .slider-small__top {
    display: flex;
  }
  .slider-small__title {
    font-size: 34px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #A0BC00;
  }
  .pop-up {
    position: relative;
    flex-shrink: 0;
    margin-left: 5px;
    &:hover {
      i svg path {
        fill: #D47AEA;
      }
      .pop-up__btn {
        opacity: 1;
        pointer-events: all;
      }
    }
    i {
      display: block;
      width: 13px;
      svg {
        width: 100%;
        height: auto;
      }
    }
    .pop-up__btn {
      position: absolute;
      left: 90%;
      bottom: 70%;
      width: 130px;
      height: 38px;
      background: #FFF;
      border: 1px solid #AB5ABF;
      border-radius: 5px;
      font-size: 15px;
      line-height: 130%;
      font-weight: 600;
      color: #AB5ABF;
      cursor: pointer;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
  }
  .slider-small__text {
    font-size: 15px;
    line-height: 130%;
    color: #3A3A3A;
  }
  .slider-small__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 10px 5px;
    margin-top: 20px;
  }
  .slider-small__price {
    display: flex;
    gap: 7px;
    span:nth-child(1) {
      font-size: 15px;
      line-height: 110%;
    }
    span:nth-child(2) {
      font-size: 40px;
      line-height: 110%;
      font-weight: 600;
      text-transform: uppercase;
    }
    span:nth-child(3) {
      font-size: 11px;
      line-height: 110%;
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
  .slider-small__btn {
    width: 135px;
    height: 44px;
    pointer-events: none;
  }
  .slider-modal {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #FFF;
    border-radius: 50px;
    padding: 50px 80px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .slider-modal--active {
    opacity: 1;
    pointer-events: all;
  }
  .slider-modal__close {
    position: absolute;
    top: 0;
    right: 60px;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E9E9E9;
    cursor: pointer;
    &:hover {
      svg {
        transform: rotate(90deg);
      }
    }
    svg {
      width: 32px;
      height: auto;
      transition: all 0.3s;
    }
  }
  .slider-modal__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
    margin-bottom: auto;
    p {
      font-size: 15px;
      line-height: 130%;
    }
    p strong {
      color: #A0BC00;
    }
  }
  .slider-modal__img {
    position: relative;
    max-width: 100%;
    margin: 50px auto 0;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      height: 130%;
      background: radial-gradient(50% 50% at 50% 50%, #D47AEA 0%, rgba(217, 217, 217, 0.00) 100%);
      opacity: 0.3;
    }
    img {
      position: relative;
      z-index: 1;
    }
  }
  .swiper-pagination {
    right: 40px;
    &-bullet {
      width: 16px;
      height: 16px;
      background: transparent;
      border: 1px solid #D47AEA;
      margin: 10px 0 !important;
      opacity: 1;
    }
    &-bullet-active {
      background: #D47AEA;
    }
  }
}
