.service-card {
  position: relative;
  color: #3A3A3A;
  &--big {
    grid-column: 2/4;
  }
  &:hover {
    .service-card__wrap {
      box-shadow: 0px 2px 45px 0px rgba(171, 90, 191, 0.30);
    }
    .service-card__img {
      opacity: 1;
    }
  }
  &__wrap {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.65);
    border: 1px solid #E9E9E9;
    border-radius: 20px;
    padding: 20px;
  }
  &__text {
    font-size: 15px;
    line-height: 130%;
    margin-bottom: auto;
  }
  &__title {
    font-size: 20px;
    line-height: 130%;
    font-weight: 900;
    text-transform: uppercase;
    margin-top: 20px;
    margin-bottom: auto;
  }
  &__check {
    margin-top: 20px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-color: #A4BF02;
      }
    }
    label {
      position: relative;
      font-size: 15px;
      line-height: 130%;
      padding-left: 30px;
      &:after {
