.content {
  overflow: hidden;
}
.btn {
  font-family: OpenSans;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-green {
  background: #C5EB2C;
  border-radius: 2px;
  &:hover {
    background: #8b9e12;
    span {
      color: #ffffff;
    }
  }
  span {
    font-size: 16px;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
    color: #000000;
  }
}
.display2 {
  position: relative;
  padding-left: 70px;
  &:after {
    content: '';
    position: absolute;
    top: 3px;
    left: 0;
    width: 13px;
    height: 13px;
    background: #000000;
  }
  span {
    font-family: OpenSans;
    font-size: 16px;
    line-height: 130%;
    font-weight: 600;
    text-transform: uppercase;
  }
  h2 {
    font-size: 36px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 30px;
  }
}
.display3 {
  font-size: 20px;
  line-height: 130%;
  font-weight: 800;
  text-transform: uppercase;
}
.form {
  &-grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    gap: 24px;
  }
  &-group--100 {
    grid-column: 1/4;
  }
  &-group {
    position: relative;
    border: none;
    padding: 0;
    margin: 0;
    input {
      width: 100%;
      height: 55px;
      background: #FFF;
      border: 1px solid #DBDBDB;
      border-radius: 5px;
      padding: 0 20px;
      font-size: 14px;
      line-height: 130%;
      color: #3A3A3A;
      &:focus {
        border-color: #A4BF02;
      }
    }
    input:focus ~ label {
      top: -24px;
      transform: unset;
      opacity: 1;
    }
    textarea {
      width: 100%;
      height: 92px;
      background: #FFF;
      border: 1px solid #DBDBDB;
      border-radius: 5px;
      padding: 20px;
      resize: none;
      &:focus {
        border-color: #A4BF02;
      }
    }
    textarea:focus ~ label {
      top: -24px;
      transform: unset;
      opacity: 1;
    }
    label {
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translate(0, -50%);
      font-size: 14px;
      line-height: 130%;
      color: #3A3A3A;
      opacity: 0.4;
      pointer-events: none;
      transition: all 0.2s;
      white-space: nowrap;
    }
    .star {
      position: absolute;
      top: -5px;
      left: 100%;
      width: 9px;
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    margin-top: 24px;
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      display: inline-block;
      font-size: 15px;
      line-height: 130%;
      color: rgba(58, 58, 58, 0.4);;
      padding-left: 24px;
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 18px;
        height: 18px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: #C5EB2C;
        border-radius: 2px;
        transition: all 0.3s;
      }
      a {
        position: relative;
        z-index: 2;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
  &-btn {
    width: 244px;
    height: 44px;
  }
}
.form-group--100 {
  label {
    top: 20px;
    transform: unset;
  }
}
