.cap-anim {
  transform: translate(-1500px, 0) rotate(90deg);
  transition: all 2s;
}
.cap-anim.active {
  transform: translate(-50%, 0) rotate(0);
}
.glasses-anim {
  transform: translate(1000px, 0) rotate(90deg);
  transition: all 1.5s;
}
.glasses-anim.active {
  transform: translate(50%, 0) rotate(0);
}


@media screen and (max-width: 1400px) {
  .display2 {
    padding-left: 40px;
    h2 {
      font-size: 28px;
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .display3 {
    font-size: 18px;
  }
}

@media screen and (max-width: 992px) {
  .display2 {
    h2 {
      font-size: 24px;
      margin-top: 15px;
    }
  }
  .display3 {
    font-size: 16px;
  }
  .form {
    &-grid {
      grid-template-columns: 100%;
    }
    &-group {
      input {
        padding: 0 10px;
      }
      textarea {
        padding: 10px;
      }
      label {
        left: 10px;
        right: 10px;
      }
    }
    &-group--100 {
      grid-column: 1;
    }
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    padding-left: 20px;
    &:after {
      top: 4px;
      width: 8px;
      height: 8px;
    }
    span {
      font-size: 14px;
    }
    h2 {
      font-size: 20px;
      margin-top: 15px;
    }
  }
  .display3 {
    font-size: 14px;
  }
  .form {
    &-bottom {
      flex-direction: column;
      align-items: start;
    }
  }
}